.loader
    position: fixed
    top: 0
    bottom: 0
    left: 0
    right: 0
    height: 100%
    width: 100%
    z-index: 1000
    img
        width: 100%
        height: 100%
        object-fit: cover
    &.hidden
        animation: hideLoader 1s
        animation-fill-mode: forwards

@keyframes hideLoader
    100%
        opacity: 0
        visibility: hidden
        pointer-events: none
    
