@use '../abstracts/variables' as var
.header
    border-bottom: 1px solid #ddd
    &__container
        background: #fff
        // max-width: 1280px
        margin: 0 auto
        padding: 20px
    &__wrapper
        display: flex
        align-items: center
        justify-content: space-between
        .logo
            h1
                margin: 0
                a
                    color: #000
                    text-decoration: none
        nav.desktopNav
            @media screen and (max-width: 768px)
                display: none
            ul
                display: flex
                gap: 20px
                li
                    a
                        display: inline-flex
                        font-weight: 700
                        letter-spacing: 1px
                        color: #000
                        transition: .3s ease-in-out
                        padding: 10px
                        border-radius: 4px
                        &:hover,&.active
                            color: #fff
                            background: var.$primaryColor
        .menu
            display: none
            @media screen and (max-width: 768px)
                display: inline-flex
            i
                font-size: 25px
.mobileNav
    height: 0
    background: #fff
    border: 1px solid #ddd
    text-align: center
    overflow: hidden 
    transition: .5s ease-out
    ul 
        padding: 20px
        li
            a
                display: inline-flex
                margin: 10px
                font-weight: 700
                letter-spacing: 1px
                color: #000
                transition: .3s ease-in-out
                padding: 10px
                border-radius: 4px
                &:hover,&.active
                    color: #fff
                    background: var.$primaryColor
.showNav
    height: 273.562px