.banner
    width: 100%
    &__container
        width: 100%
    &__wrapper
        width: 100%
        height: 100vh
        position: relative
        display: flex
        align-items: center
        @media screen and (max-width: 768px)
            height: auto
    &__image
        width: 100%
        height: 100%
        position: absolute
        top: 0
        z-index: -1
        img
            width: 100%
            height: 100%
            object-fit: cover
    &__overlay
        background: rgba(0,0,0,0.6)
        position: absolute
        top: 0
        left: 0
        right: 0
        bottom: 0
    &__content
        max-width: 1280px
        margin: 0 auto
        text-align: center
        padding: 150px
        @media screen and (max-width: 768px)
            padding: 200px 30px
        h1
            font-size: 50px
            color: #fff
            margin-bottom: 10px
            font-family: 'Raleway'
            @media screen and (max-width: 768px)
                font-size: 40px
        p
            font-size: 25px
            color: #fff
            font-family: 'Raleway'
            @media screen and (max-width: 768px)
                font-size: 18px