@use '../abstracts/variables'
.footer
    background: #161616
    color: #fff
    &__container
        max-width: 1480px
        margin: 0 auto
    &__wrapper
        padding: 100px 50px
        @media screen and (max-width: 768px)
            padding: 100px 50px 0 50px
    &__columns
        display: flex
        gap: 30px
        justify-content: center
        flex-wrap: wrap
        @media screen and (max-width: 768px)
            flex-direction: column
    &__column
        width: 300px
        @media screen and (max-width: 768px)
            width: 100%
            margin-bottom: 60px
        h1
            margin-bottom: 5px
        h4
            font-family: variables.$secondaryFont
            font-size: 16px
            color: #fff
            font-weight: normal
            margin-bottom: 20px
        p
            font-family: variables.$secondaryFont
            color: #999
            font-size: 15px
            line-height: 28px