@import url("https://fonts.googleapis.com/css2?family=Raleway:wght@300;400;500;600;700;800;900&family=Roboto:wght@300;400;500;700;900&display=swap");
html {
  line-height: 1.15;
  -webkit-text-size-adjust: 100%;
}

body {
  margin: 0;
}

main {
  display: block;
}

h1 {
  font-size: 2em;
  margin: 0.67em 0;
}

hr {
  box-sizing: content-box;
  height: 0;
  overflow: visible;
}

pre {
  font-family: monospace, monospace;
  font-size: 1em;
}

a {
  background-color: transparent;
}

abbr[title] {
  border-bottom: none;
  text-decoration: underline;
  text-decoration: underline dotted;
}

b,
strong {
  font-weight: bolder;
}

code,
kbd,
samp {
  font-family: monospace, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}

sub {
  bottom: -0.25em;
}

sup {
  top: -0.5em;
}

img {
  border-style: none;
}

button,
input,
optgroup,
select,
textarea {
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
  margin: 0;
}

button,
input {
  overflow: visible;
}

button,
select {
  text-transform: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  -webkit-appearance: button;
}

button::-moz-focus-inner,
[type=button]::-moz-focus-inner,
[type=reset]::-moz-focus-inner,
[type=submit]::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

button:-moz-focusring,
[type=button]:-moz-focusring,
[type=reset]:-moz-focusring,
[type=submit]:-moz-focusring {
  outline: 1px dotted ButtonText;
}

fieldset {
  padding: 0.35em 0.75em 0.625em;
}

legend {
  box-sizing: border-box;
  color: inherit;
  display: table;
  max-width: 100%;
  padding: 0;
  white-space: normal;
}

progress {
  vertical-align: baseline;
}

textarea {
  overflow: auto;
}

[type=checkbox],
[type=radio] {
  box-sizing: border-box;
  padding: 0;
}

[type=number]::-webkit-inner-spin-button,
[type=number]::-webkit-outer-spin-button {
  height: auto;
}

[type=search] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

[type=search]::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

details {
  display: block;
}

summary {
  display: list-item;
}

template {
  display: none;
}

[hidden] {
  display: none;
}

*, *:after, *:before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

h1 {
  font-family: "Raleway", sans-serif;
  font-weight: 900;
  margin: 0;
}

a {
  text-decoration: none;
  font-family: "Raleway", sans-serif;
}

li {
  list-style: none;
}

.loader {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  height: 100%;
  width: 100%;
  z-index: 1000;
}
.loader img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.loader.hidden {
  animation: hideLoader 1s;
  animation-fill-mode: forwards;
}

@keyframes hideLoader {
  100% {
    opacity: 0;
    visibility: hidden;
    pointer-events: none;
  }
}
.btn {
  border: none;
  background: none;
  outline: none;
  display: inline-flex;
  border: 1px solid #ddd !important;
  padding: 10px 26px;
  color: #006aff;
  font-family: "Raleway", sans-serif;
  border-radius: 4px;
  font-weight: 700;
  font-size: 16px;
  align-items: center;
  justify-content: center;
  transition: 0.2s ease;
  margin: 16px 0;
}
.btn:hover {
  cursor: pointer;
  background: #006aff;
  color: #fff;
  border: 1px solid #006aff !important;
}

form {
  width: 100%;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  gap: 25px;
  padding: 20px;
}
form .formHeading h1 {
  font-size: 32px;
  font-family: "Roboto", sans-serif;
  font-weight: 400;
  margin: 0;
}
form .inputFlexContainer {
  display: flex;
  gap: 20px;
}
@media screen and (max-width: 520px) {
  form .inputFlexContainer {
    flex-direction: column;
  }
}
form .inputContainer {
  display: flex;
  flex-direction: column;
  width: 100% !important;
}
form .inputContainer label {
  font-size: 13px;
  margin-bottom: 5px;
  color: #333;
  font-family: "Roboto", sans-serif;
}
form .inputContainer label span {
  color: #666;
}
form .inputContainer input, form .inputContainer textarea, form .inputContainer select, form .inputContainer button {
  border: 1px solid #ddd;
  border-radius: 4px;
  padding: 10px;
  font-family: "Roboto", sans-serif;
  font-weight: 400;
  color: #333;
  transition: all 0.2s ease-;
  background: #fff;
}
form .inputContainer input:focus, form .inputContainer textarea:focus, form .inputContainer select:focus, form .inputContainer button:focus {
  border: 1px solid blue;
  outline: none;
}
form .inputContainer input::placeholder, form .inputContainer textarea::placeholder, form .inputContainer select::placeholder, form .inputContainer button::placeholder {
  font-family: "Roboto", sans-serif;
  font-weight: 400;
}
form .customSelectMenu {
  position: relative;
}
form .customSelectMenu .selectButton {
  background: none;
  text-align: left;
  width: 100%;
  background: #fff;
}
form .customSelectMenu .customSelectOptions {
  border: 1px solid #ddd;
  border-radius: 4px;
  position: absolute;
  width: 100%;
  background: #fff;
  z-index: 100;
}
form .customSelectMenu .customSelectOptions .option {
  display: none;
}
form .customSelectMenu .customSelectOptions .select-item {
  background: #fff;
  font-size: 16px;
  padding: 10px;
  font-family: "Roboto", sans-serif;
  font-weight: 400;
  color: #333;
  margin: 0;
}
form .customSelectMenu .customSelectOptions .select-item:hover {
  background: #f2f2f2;
}
form .customSelectMenu .customSelectOptions.hideOptions {
  display: none;
}

.header {
  border-bottom: 1px solid #ddd;
}
.header__container {
  background: #fff;
  margin: 0 auto;
  padding: 20px;
}
.header__wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.header__wrapper .logo h1 {
  margin: 0;
}
.header__wrapper .logo h1 a {
  color: #000;
  text-decoration: none;
}
@media screen and (max-width: 768px) {
  .header__wrapper nav.desktopNav {
    display: none;
  }
}
.header__wrapper nav.desktopNav ul {
  display: flex;
  gap: 20px;
}
.header__wrapper nav.desktopNav ul li a {
  display: inline-flex;
  font-weight: 700;
  letter-spacing: 1px;
  color: #000;
  transition: 0.3s ease-in-out;
  padding: 10px;
  border-radius: 4px;
}
.header__wrapper nav.desktopNav ul li a:hover, .header__wrapper nav.desktopNav ul li a.active {
  color: #fff;
  background: #006aff;
}
.header__wrapper .menu {
  display: none;
}
@media screen and (max-width: 768px) {
  .header__wrapper .menu {
    display: inline-flex;
  }
}
.header__wrapper .menu i {
  font-size: 25px;
}

.mobileNav {
  height: 0;
  background: #fff;
  border: 1px solid #ddd;
  text-align: center;
  overflow: hidden;
  transition: 0.5s ease-out;
}
.mobileNav ul {
  padding: 20px;
}
.mobileNav ul li a {
  display: inline-flex;
  margin: 10px;
  font-weight: 700;
  letter-spacing: 1px;
  color: #000;
  transition: 0.3s ease-in-out;
  padding: 10px;
  border-radius: 4px;
}
.mobileNav ul li a:hover, .mobileNav ul li a.active {
  color: #fff;
  background: #006aff;
}

.showNav {
  height: 273.562px;
}

.banner {
  width: 100%;
}
.banner__container {
  width: 100%;
}
.banner__wrapper {
  width: 100%;
  height: 100vh;
  position: relative;
  display: flex;
  align-items: center;
}
@media screen and (max-width: 768px) {
  .banner__wrapper {
    height: auto;
  }
}
.banner__image {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  z-index: -1;
}
.banner__image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.banner__overlay {
  background: rgba(0, 0, 0, 0.6);
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}
.banner__content {
  max-width: 1280px;
  margin: 0 auto;
  text-align: center;
  padding: 150px;
}
@media screen and (max-width: 768px) {
  .banner__content {
    padding: 200px 30px;
  }
}
.banner__content h1 {
  font-size: 50px;
  color: #fff;
  margin-bottom: 10px;
  font-family: "Raleway";
}
@media screen and (max-width: 768px) {
  .banner__content h1 {
    font-size: 40px;
  }
}
.banner__content p {
  font-size: 25px;
  color: #fff;
  font-family: "Raleway";
