.newPlots {
  margin: 50px 0;
}
.newPlots__container {
  max-width: 1480px;
  margin: 0 auto;
}
.newPlots__wrapper {
  padding: 0 20px;
}
.newPlots__wrapper h2 {
  padding-bottom: 10px;
  border-bottom: 1px solid #d1d1d7;
  margin: 20px 10px;
  font-weight: normal;
  font-family: "Raleway";
}
.newPlots .viewMore {
  text-align: center;
  display: flex;
  max-width: 300px;
  margin: 25px auto;
  border: 1px solid #006aff !important;
}
.newPlots__plots {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
}
.newPlots__plots .plot {
  width: calc(25% - 20px);
  box-shadow: rgba(0, 0, 0, 0.3) 0px 2px 4px 0px;
  border-radius: 4px;
}
@media screen and (max-width: 1024px) {
  .newPlots__plots .plot {
    width: calc(33.3333333333% - 20px);
  }
}
@media screen and (max-width: 768px) {
  .newPlots__plots .plot {
    width: calc(50% - 20px);
  }
}
@media screen and (max-width: 500px) {
  .newPlots__plots .plot {
    width: calc(100% - 20px);
  }
}
.newPlots__plots .plot__image {
  width: 100%;
  height: 250px;
}
.newPlots__plots .plot__image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.newPlots__plots .plot__content {
  padding: 20px;
}
.newPlots__plots .plot__details {
  display: flex;
  gap: 20px;
  margin-bottom: 10px;
  flex-wrap: wrap;
}
.newPlots__plots .plot__details p {
  font-family: "Roboto";
}
.newPlots__plots .plot__name {
  margin-bottom: 10px;
}
.newPlots__plots .plot__name h3 {
  font-size: 24px;
  font-family: "Raleway";
  font-weight: 800;
}
.newPlots__plots .plot__address {
  margin-bottom: 10px;
}
.newPlots__plots .plot__address p {
  font-family: "Roboto";
}
.newPlots__plots .plot__view a {
  display: inline-flex;
  align-items: center;
  gap: 5px;
  font-weight: bold;
  color: #006aff;
}

