.newPlots
    margin: 50px 0
    &__container
        max-width: 1480px
        margin: 0 auto
    &__wrapper
        padding: 0 20px
        h2
            padding-bottom: 10px
            border-bottom: 1px solid #d1d1d7
            margin: 20px 10px
            font-weight: normal
            font-family: 'Raleway'
    .viewMore
        text-align: center
        display: flex
        max-width: 300px
        margin: 25px auto
        border: 1px solid #006aff !important
    &__plots
        display: flex
        flex-wrap: wrap
        justify-content: center
        gap: 20px
        .plot
            width: calc( (100% / 4) - 20px )
            box-shadow: rgb(0 0 0 / 30%) 0px 2px 4px 0px
            border-radius: 4px
            @media screen and (max-width: 1024px)
                width: calc( (100% / 3) - 20px )
            @media screen and (max-width: 768px)
                width: calc( (100% / 2) - 20px )
            @media screen and (max-width: 500px)
                width: calc( (100% / 1) - 20px )
            &__image
                width: 100%
                height: 250px
                img
                    width: 100%
                    height: 100%
                    object-fit: cover
            &__content
                padding: 20px
            &__details
                display: flex
                gap: 20px
                margin-bottom: 10px
                flex-wrap: wrap
                p
                    font-family: 'Roboto'
            &__name
                margin-bottom: 10px
                h3
                    font-size: 24px
                    font-family: 'Raleway'
                    font-weight: 800
            &__address
                margin-bottom: 10px
                p
                    font-family: 'Roboto'
            &__view
                a
                    display: inline-flex
                    align-items: center
                    gap: 5px
                    font-weight: bold
                    color: #006aff
                
