@use '../abstracts/variables'
.contact

    &__header
        background: #161616
        padding: 100px 0
        @media screen and (max-width: 600px)
            padding: 40px 10px
        &--wrapper
            max-width: 1140px
            margin: 0 auto
        h1
            font-family: variables.$secondaryFont
            font-weight:  700
            font-size: 80px
            color: #fff
            padding: 50px 20px
            @media screen and (max-width: 600px)
                font-size: 40px
    &__details
        display: flex
        width: 100%
        justify-content: center
        max-width: 1140px
        margin: 0 auto
        padding: 50px 20px
        @media screen and (max-width: 768px)
            flex-direction: column
    &__info
        padding: 20px
        flex: .8
        h4 
            font-family: variables.$secondaryFont
            font-size: 26px
            color: #222
            margin-bottom: 50px
    &__phone,&__mail,&__location
        display: flex
        gap: 30px
        margin: 20px 0 30px 0
        span
            i 
                color: #37383a
        p
            flex: 1
            font-size: 15px
            font-family: variables.$secondaryFont
            color: #37383a
    &__form
        flex: 1