@use '../abstracts' as *
.btn
    border: none
    background: none
    outline: none
    display: inline-flex
    border: 1px solid #ddd !important
    padding: 10px 26px
    color: $primaryColor
    font-family: $primaryFont
    border-radius: 4px
    font-weight: 700
    font-size: 16px
    align-items: center
    justify-content: center
    transition: .2s ease
    margin: 16px 0
    &:hover
        cursor: pointer
        background: $primaryColor
        color: #fff
        border: 1px solid $primaryColor !important