@use '../abstracts' as *
form
    width: 100%
    margin: 0 auto
    display: flex
    flex-direction: column
    gap: 25px
    padding: 20px
    .formHeading
        h1
            font-size: 32px
            font-family: $secondaryFont
            font-weight: 400
            margin: 0
    .inputFlexContainer
        display: flex 
        gap: 20px
        @media screen and (max-width: 520px)
            flex-direction: column
    .inputContainer
        display: flex
        flex-direction: column
        width: 100% !important
        label
            font-size: 13px
            margin-bottom: 5px
            color: #333
            font-family: $secondaryFont
            span
                color: #666
        input,textarea,select,button
            border: 1px solid #ddd
            border-radius: 4px
            padding: 10px
            font-family: $secondaryFont
            font-weight: 400
            color: #333
            transition: all .2s ease-
            background: #fff
            &:focus
                border: 1px solid blue
                outline: none
            &::placeholder
                font-family: $secondaryFont
                font-weight: 400
    .customSelectMenu
        position: relative
        .selectButton
            background: none
            text-align: left
            width: 100%
            background: #fff
        .customSelectOptions
            border: 1px solid #ddd
            border-radius: 4px
            position: absolute
            width: 100%
            background: #fff
            z-index: 100
            .option
                display: none
            .select-item
                background: #fff
                font-size: 16px
                padding: 10px
                font-family: $secondaryFont
                font-weight: 400
                color: #333
                margin: 0
                &:hover
                    background: #f2f2f2
            &.hideOptions
                display: none
    