@use '../abstracts/variables'
.singleProperty
    &__container
        max-width: 1480px
        margin: 0 auto
    &__wrapper
        display: flex
        padding: 20px
        flex-wrap: no-wrap
        @media screen and (max-width: 1024px)
            flex-direction: column-reverse
        @media screen and (max-width: 550px)
            padding: 0
    &__details
        width: 70%
        @media screen and (max-width: 1024px)
            width: 100%
        &--wrapper
            padding: 20px
        &--video
            width: 100%
            video
                width: 100%
                height: 100%
        &--price
            padding: 20px 20px 0 20px
            display: flex
            align-items: center
            h1
                font-family: variables.$secondaryFont
                font-size: 36px
                font-weight: 700
                color: variables.$primaryColor
                margin: 0 0 18px 0
        &--dimensions
            padding: 0px 20px 20px 20px
            display: flex
            gap: 30px
            margin-bottom: 18px
            flex-wrap: wrap
            // @media screen and (max-width: 550px)
            //     flex-direction: column
            //     justify-content: flex-start
            p
                font-family: variables.$secondaryFont
                display: flex
                gap: 15px
                align-items: center
                // justify-content: center
        &--galleryContainer
            display: flex
            flex-wrap: wrap
            gap: 5px
            a
                width: 300px
                height: 300px
                flex-grow: 1
                background: #ddd
                img
                    width: 100%
                    height: 100%
                    object-fit: cover
        
    &__contact
        width: 30%
        @media screen and (max-width: 1024px)
            width: 100%
            &--wrapper
                padding: 20px 20px 0 20px
        &--details
            // height: 100%
            background: #eaeaea
            margin: 20px 0
            padding: 30px
        &--flex
            display: flex
            flex-direction: column
            @media screen and (max-width: 1024px)
                flex-direction: row
                gap: 40px
            @media screen and (max-width: 768px)
                flex-direction: column
                gap: 0
        &--heading
            font-size: 26px
            font-family: variables.$secondaryFont
            margin-bottom: 20px
        &--phone,&--mail,&--address
            display: flex
            gap: 10px
            margin: 20px 0 30px 0
            font-family: variables.$secondaryFont